import React, { Component } from 'react';

import Item from './Item'

class List extends Component {
    constructor (props) {
        super(props)

        this.state = {
            configs: ['/', '', '']
        }
    }
    render() {
        return (
            <ul>
                {
                    this.state.configs.map(config => (
                        <Item key={ config } config={ config } />
                    ))
                }
            </ul>
        );
    }
}

export default List;


